  margin-top: 20px;
  margin-bottom: 14px;
  margin-left: 6px;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.cocktail-description {
  color: var(--primary-text-color-theme-light);
  font-size: 14px;
  font-family: var(--primary-font);
  word-wrap: break-word;
  text-overflow: ellipsis;
