}

.button-learn-more {
  box-sizing: border-box;
  background-color: var(--bg-card-color-theme-light);
  color: var(--primary-text-color-theme-light);
  letter-spacing: .28px;
  text-transform: uppercase;
  width: 232px;
  height: 48px;
  border: 1px solid #7e8fdd;
  border-radius: 12px;
  margin-right: 13px;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  box-shadow: 0 4px 46px rgba(0, 0, 0, .05);
}

.button-add-fav {
  background: var(--primary-accent-color);
  width: 48px;
  height: 48px;
  border-style: none;
  border-radius: 12px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.icon-add-fav {
  width: 18px;
  height: 18px;
  stroke: var(--bg-card-color-theme-light);
  fill: none;
}

.button-add-fav:hover > .icon-add-fav, .button-add-fav-active > .icon-add-fav {
  fill: var(--bg-card-color-theme-light);
}

